import React from 'react';

import {  PanelHeader,  Avatar, Div, HeaderButton, List, Cell, Snackbar, Group, InfoRow, Progress} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';



const orangeBackground = {
  backgroundImage: 'linear-gradient(135deg, #ffb73d, #ffa000)'
};

const blueBackground = {
  backgroundColor: 'var(--accent)'
};

class Questions extends React.Component {


	render(){
		return(
			<Div style={{"background": "linear-gradient(to right, #F6EFD2, #CEAD78)"}}>
              <List>
                {this.props.quests.map((name, i) => <Cell before={<Avatar />} key={i} style={{"background": "linear-gradient(to right, #8B04A8, #E9003A)"}}><InfoRow title={name.key}> <Progress  value={76} style={{"color": "green"}}/></InfoRow></Cell>)}
              </List>
              </Div>
			);
	}
}
export default Questions;
